<template>
  <div class="home text-white">
    <div class="d-flex justify-content-center p-5">
      <img src="@/assets/img/main-logo.png" class="main-logo mt-3" />
    </div>
    <div class="text-white text-center">
      <h4>Welcome to</h4>
      <h4 class="mb-3">#CovidYogaChallenge</h4>
    </div>
    <div>
      <form>
        <!-- eslint-disable -->
        <div class="form-group">
          <input
            type="text"
            placeholder="Name"
            v-model="username"
            :class="[
              'form-control form-rounded mb-4',
              errs.username ? 'is-invalid' : '',
            ]"
          />
          <div
            class="invalid-feedback"
            v-if="errs.username"
            :style="{ marginTop: '-15px', marginLeft: '10px' }"
          >
            {{ errs.username }}
          </div>
        </div>
        <div class="form-group">
          <input
            type="email"
            v-model="email"
            placeholder="Email"
            :class="[
              'form-control form-rounded mb-4',
              errs.email ? 'is-invalid' : '',
            ]"
          />
          <div
            class="invalid-feedback"
            v-if="errs.email"
            :style="{ marginTop: '-15px', marginLeft: '10px' }"
          >
            {{ errs.email }}
          </div>
        </div>
        <p class="intro-text">
          By signing up for challenge, you agree that you have understood the
          <a href>terms and conditions</a> ,
          <a href>privacy policy</a>
        </p>
      </form>
    </div>
    <div
      :class="
        `
      d-flex 
      text-center 
      text-uppercase 
      continue-footer 
      justify-content-between
      pt-4 font-weight-bolder 
      `
      "
      @click="setUserProfile"
    >
      <div class="footer-block" />
      <div class="footer-block font-weight-bold">CONTINUE</div>
      <div class="footer-block pr-4">
        <button class="btn btn-transparent text-white">
          <i class="ti-arrow-right footer-icon" />
        </button>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import { mapActions } from "vuex";
export default {
  name: "Home",
  data: () => ({
    email: "",
    username: "",
    errs: {
      email: "",
      username: "",
      status: false,
    },
  }),
  methods: {
    ...mapActions({
      setUser: "setUser",
    }),
    setUserProfile() {
      this.validateUser();
      if (!this.errs.status) {
        this.errs = {
          email: "",
          username: "",
          status: false,
        };
        let { username, email } = this;
        this.setUser({ username, email });
      }
    },
    validateUser() {
      let { username, email } = this;
      if (!username) {
        this.errs.status = true;
        this.errs.username = "Name is required.";
      }
      if (!email) {
        this.errs.status = true;
        this.errs.email = "Email is required.";
      }
      return;
    },
  },
};
</script>

